import Axios from 'axios';

export default {
    registerAccount: async (nome, email, password) => {
        const msg = {
            nome: nome,
            email: email,
            password: password
        }
        const response = await Axios.post('/account/signUp', msg);

        return response.data;
    },

    requestRecoverPassword: async(email) => {
        const response = await Axios.post('/account/requestRecoverPassword', {email: email});

        return response.data;
    },

    changePassword: async(password, email, code) => {
        const response = await Axios.post('/account/api/changePassword', {
            password: password,
            email: email,
            code: code
        });

        return response.data;
    },

    checkEmail: async (email) => {
        var re = /\S+@\S+\.\S+/;
        return re.test(email);
    }
}